.title {
  display: inline-block;
  position: absolute;
  top: 50px;
  left: 0px;
  right: 0px;
  width: 300px;
  margin: 0 auto;
  font-size: 60px;
  line-height: 1.2em;
  text-align: center;
  color: white; }

.description {
  display: inline-block;
  position: absolute;
  top: 50px;
  left: 50px;
  width: 300px;
  font-size: 16px;
  line-height: 1.2em;
  text-align: left;
  color: white; }

.target {
  display: inline-block;
  position: absolute;
  color: red;
  top: 130px;
  left: 50px;
  width: 200px;
  font-size: 20px;
  line-height: 1.2em; }

.hit {
  display: inline-block;
  position: absolute;
  color: red;
  top: 170px;
  left: 50px;
  width: 200px;
  font-size: 20px;
  line-height: 1.2em; }

.score {
  display: inline-block;
  position: absolute;
  color: red;
  top: 210px;
  left: 50px;
  width: 200px;
  font-size: 20px;
  line-height: 1.2em; }

.time {
  display: inline-block;
  position: absolute;
  color: yellow;
  top: 250px;
  left: 50px;
  width: 200px;
  font-size: 20px;
  line-height: 1.2em; }

.centerBlock {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  -webkit-transform: scale(2);
          transform: scale(2); }

.fail, .success {
  position: fixed;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 2; }
  .fail p, .success p {
    display: inline-block;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 500px;
    height: 300px;
    margin-left: -250px;
    margin-top: -150px;
    font-size: 60px;
    line-height: 100px;
    color: red;
    vertical-align: middle; }
